import Component from 'vue-class-component';
import ynowApi from '../../api/ynow';

@Component
export default class AccountView extends Vue {
  list = null
  mounted () {
    ynowApi.getDemandList().then(res => {
      this.list = res.data.list;
    });
  }
};



